.dragdrop {
  &__item,
  &__drag-item {
    cursor: move;
  }

  &__item {
    user-select: none;

    &--dragging {
      opacity: 0.1;
    }
  }

  &__drag-item {
    position: fixed;
    z-index: 1000;
    user-select: none;
    cursor: move;
    will-change: transform;
    pointer-events: none;
    touch-action: none;
    -webkit-user-drag: none;
    transform: translate3d(var(--translateX), var(--translateY), 0);
  }
}

.disable-select-all {
  * {
    user-select: none !important;
  }
}
